.panel {
  position: relative;
  padding: 0 20px;
}

.panel h1, .panel p.intro {
  text-align: center;
}

.panel p {
  font-size: 21px;
  color: #aeaeae;
}

.panel p.intro {
  -webkit-font-smoothing: antialiased;
  color: #aeaeae;
  font-family: "Miriam Libre", sans-serif;
  font-size: 21px;
  letter-spacing: -.02em;
}

.panel.dark {
  background-color: #f0f2f1;
}

.panel.standout {
  background-color: #159f23;
  color: #fff;
}

.panel.statement {
  display: table;
  table-layout: fixed;
  height: 100vh;
  min-height: 900px;
  margin: 0 0 -100px 0;
  width: 100%;
  text-align: center;
}

.panel.statement .content {
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  padding-bottom: 150px;
}

.panel.statement h1 {
  font-size: 56px;
}

.panel.statement h1 + p {
  font-size: 28px;
  font-weight: 100;
  -webkit-font-smoothing: subpixel-antialiased;
}

.panel.statement p.caption {
  color: #525252;
  font-size: 18px;
}

.panel.statement .browser-window {
  display: block;
  margin: 8vh auto 15px;
}

.panel.statement .next-up {
  bottom: 150px;
}

.panel.laracon {
  background: #159f23 url("/assets/img/laracon-bg.jpg") center center no-repeat;
  padding: 60px 0;
  text-align: center;
  box-shadow: 0 0 35px rgba(0, 0, 0, 0.3);
  position: relative;
  z-index: 5;
}

.panel.laracon > h1 {
  color: #fff;
  margin: 0 0 30px 0;
  text-align: center;
}

.panel.laracon h2 {
  font-weight: 400;
  margin: 40px 0;
}

.panel.laracon .date {
  font-size: 16px;
  letter-spacing: .2em;
  text-transform: uppercase;
  font-weight: 400;
}

.panel.laracon .btn {
  background: rgba(255, 255, 255, 0.85);
  border-radius: 60px;
  color: #159f23;
  margin: 10px;
  width: 260px;
  font-size: 18px;
}

.panel.laracon .btn:hover {
  background: #fff;
}

.panel.laracon .btn em {
  font-size: 24px;
  display: block;
  font-style: normal;
}

.panel.features {
  padding: 75px 0;
}

.panel.features > h1,
.panel.features > p {
  margin: 0 0 20px 0;
  text-align: center;
}

.panel.features .blocks {
  max-width: 900px;
  background: #fff;
  margin: 50px auto;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
}

.panel.features .blocks .block {
  border-bottom: 1px solid #dbdcdb;
  height: 225px;
  overflow: hidden;
}

.panel.features .blocks .block:after {
  content: "";
  display: table;
  clear: both;
}

.panel.features .blocks .block .text {
  padding: 40px;
  width: 60%;
  float: left;
}

.panel.features .blocks .block .text h2 {
  font-size: 24px;
  font-weight: 400;
  color: #159f23;
}

.panel.features .blocks .block .media {
  float: right;
  padding-top: 20px;
  width: 40%;
  overflow: hidden;
}

.panel.features .blocks .block .media .browser-window {
  width: 400px;
}

.panel.features .blocks .block.even .text {
  float: right;
}

.panel.features .blocks .block.even .media {
  float: left;
}

.panel.features .blocks .block.even .media .terminal-window {
  float: left;
  margin-left: -5px;
  width: 360px;
}

.panel.features .blocks .block.even .media .terminal-window pre[class*="language-"] {
  border-radius: 0;
  margin-top: 0;
}

.panel.features .blocks .block p {
  font-size: 14px;
  line-height: 1.8;
  color: #80878c;
}

.panel.partners {
  padding: 50px 0 25px 0;
}

.panel.ecosystem {
  padding: 150px 0 50px;
  background: url("/assets/img/laravel-tucked.png") center top no-repeat;
}

.panel.ecosystem > h1 {
  margin: 0 0 20px 0;
  text-align: center;
}

.panel .browser-window,
.panel .terminal-window {
  overflow: hidden;
}

.panel .browser-window pre[class*="language-"], .panel .browser-window .window-content,
.panel .terminal-window pre[class*="language-"],
.panel .terminal-window .window-content {
  overflow: hidden;
}

@media (max-width: 980px) {
  .panel.features {
    padding: 75px 0;
  }
}

@media (max-width: 760px) {
  .panel.statement h1 {
    font-size: 42px;
    line-height: 1.2;
  }
  .panel.statement h1 + p {
    font-size: 21px;
  }
  .panel.ecosystem .forge {
    text-align: center;
    padding: 0 50px;
    margin: 50px 0;
  }
  .panel.ecosystem .forge .screenshot {
    float: none;
    margin-bottom: 25px;
    max-width: 100%;
  }
  .panel.ecosystem .forge .screenshot:after {
    content: "";
    display: table;
    clear: both;
  }
  .panel.ecosystem .forge .content {
    max-width: 100%;
  }
  .panel.ecosystem .forge .content:after {
    content: "";
    display: table;
    clear: both;
  }
  .panel.features .blocks .block .text {
    padding: 15px;
  }
  .panel.features .blocks .block .text p {
    margin-bottom: 0;
  }
}

@media (max-width: 580px) {
  .panel.features .blocks .block {
    height: 410px;
