  .panel.features .blocks .block .text {
    width: 100%;
    float: none !important;
    display: block;
    padding: 5%;
  }
  .panel.features .blocks .block .media {
    float: none !important;
    display: block;
    width: 100%;
  }
  .panel.features .blocks .block .media .terminal-window,
  .panel.features .blocks .block .media .browser-window {
    width: 90% !important;
    margin: 0 5% !important;
    float: none !important;
  }
}

.browser-window, .terminal-window {
  text-align: left;
  margin: 20px;
  width: 602px;
  height: 355px;
  display: inline-block;
  border-radius: 4px;
  background-color: #fff;
  border: 1px solid #ddd;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  overflow: overlay;
}

.browser-window .top-bar, .terminal-window .top-bar {
  height: 30px;
  border-radius: 4px 4px 0 0;
  border-top: thin solid #eaeae9;
  border-bottom: thin solid #dfdfde;
  background: #ebebeb;
}

.browser-window .circle, .terminal-window .circle {
  height: 8px;
  width: 8px;
  display: inline-block;
  border-radius: 50%;
  background-color: white;
}

.browser-window .circles, .terminal-window .circles {
  margin: 1px 10px;
}

.browser-window .window-content, .terminal-window .window-content {
  margin: 0;
  width: 100%;
  min-height: 90%;
  display: inline-block;
  border-radius: 0 0 4px 4px;
}

.browser-window .window-content pre[class*="language-"] {
  background: #fff;
  margin: 0;
}

.sidebar {
  border-right: 1px solid #f0f2f1;
  width: 250px;
  float: left;
  padding: 35px;
}

.sidebar h2 {
  position: relative;
  font-size: 14px;
  font-weight: 400;
  padding: 0.75rem 0.75rem 0.75rem 0;
  margin: 0;
  cursor: pointer;
  transition: color 0.1s linear;
  border-bottom: 1px dashed rgba(0, 0, 0, 0.1);
  font-family: sans-serif "Whitney SSm A", "Whitney SSm B";
}

.sidebar h2:focus {
  outline: 0;
}

.sidebar h2:hover {
  color: #f4645f;
}

.sidebar h2:after {
  content: "";
  position: absolute;
  right: 0;
  top: 1.5rem;
  width: 0;
  height: 0;
  border-top: 4px solid transparent;
  border-right: 4px solid transparent;
  border-left: 4px solid #dee0df;
  border-bottom: 4px solid transparent;
}

.sidebar h2 + ul {
  display: none;
}

.sidebar h2.is-active:after {
  transform: rotate(90deg);
}

.sidebar h2.is-active + ul {
  display: block;
}

.sidebar > ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar > ul > li {
  font-size: 13px;
  font-weight: 400;
  padding: 0;
  margin: 0;
  line-height: 30px;
}

.sidebar > ul > li > ul {
  list-style: none;
  margin: 0.5rem 0 0;
  padding: 0;
  font-size: 13px;
  padding-left: 0.75rem;
}

.sidebar a {
  line-height: 30px;
}

.sidebar a:hover {
  color: #8dd02a;
}

@media (max-width: 1080px) {
  .sidebar {
    padding: 25px;
    width: 200px;
