.community .hero .content {
  text-align: center;
}

.community .hero .content p {
  -webkit-font-smoothing: antialiased;
  color: #7b7b7b;
  font-family: "Miriam Libre", sans-serif;
  font-size: 21px;
  line-height: 40px;
  margin-bottom: 60px;
  letter-spacing: -.02em;
}

.community .container {
  padding: 0 20px;
}

.community h2 {
  color: #159f23;
  font-weight: 400;
  -webkit-margin-before: 0em;
}

.community h3 {
  font-weight: 400;
}

.community h4 {
  font-family: "Miriam Libre", sans-serif;
  color: #aeaeae;
  -webkit-margin-before: 2.5em;
  -webkit-margin-after: 1em;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.mw-620px {
  max-width: 900px;
  margin: 0 auto;
}

.partner-card {
  display: flex;
  margin-bottom: 20px;
}

.partner-card:hover {
  border-color: transparent;
  border-radius: 0;
  box-shadow: 0 2px 7px rgba(0, 0, 0, 0.2);
}

.partner-card-banner {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.partner-card-lg {
  width: 100%;
  height: 300px;
  background-color: #fff;
}

.partner-card-md {
  width: 440px;
  height: 300px;
  background-color: #fff;
}

.partner-card-sm {
  width: 300px;
  height: 300px;
  background-color: #fff;
}

.partner-card-sm:hover {
  border-color: transparent;
  border-radius: 0;
  box-shadow: 0 2px 7px rgba(0, 0, 0, 0.2);
}

.mr-2 {
  margin-right: 20px;
}

.mb-2 {
  margin-bottom: 20px;
}

.text-red {
  color: #159f23;
}

.partner-card-body {
  padding: 20px;
}

.partner-card-body p {
  color: #444444;
  font-size: 15px;
  line-height: 1.6;
  margin-bottom: 10px;
  font-weight: 400;
}

.callout-partner {
  height: 100%;
  padding: 0;
  margin: 0;
}

.partner-logo-banner {
  max-width: 100%;
  height: 160px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.partner-card-sponsor {
  text-transform: uppercase;
  font-size: 15px;
  margin-bottom: 10px;
  font-weight: 600;
  color: #aeaeae;
}

.partner-logo-banner-vehikl {
  background-color: #DD5B01;
  background: url(/assets/img/partner-img-vehikl-color.png);
  background-size: 900px 300px;
  background-repeat: no-repeat;
}

.partner-logo-banner-tighten {
  background-color: #FFA02B;
  background: url(/assets/img/partner-img-tighten-color.png);
  background-size: 900px 300px;
  background-repeat: no-repeat;
}

.partner-logo-banner-kirschbaum {
  background-color: #FFA02B;
  background: url(/assets/img/partner-img-kirschbaum-color.png);
  background-size: 900px 300px;
  background-repeat: no-repeat;
}

.partner-logo-banner-byte5 {
  background-color: #FFA02B;
  background: url(/assets/img/partner-img-byte5-color.png);
  background-size: 900px 300px;
  background-repeat: no-repeat;
}

.partner-logo-banner-64robot {
  background-color: #FFA02B;
  background: url(/assets/img/partner-img-64robot-color.png);
  background-size: 900px 300px;
  background-repeat: no-repeat;
}

.partner-logo-banner-cubet {
  background-color: #FFA02B;
  background: url(/assets/img/partner-img-cubet-color.png);
  background-size: 500px 305px;
  background-repeat: no-repeat;
}

.partner-logo-banner-devsquad {
  background-color: #FFA02B;
  background: url(/assets/img/partner-img-devsquad-color.png);
  background-size: 900px 300px;
  background-repeat: no-repeat;
}

.partner-logo-banner-insider {
  background-color: #FFA02B;
  background: url(/assets/img/partner-img-insider.png);
  background-size: 900px 300px;
  background-position: center;
  background-repeat: no-repeat;
}

.partner-banner-become-title {
  background-color: #00A8AA;
  color: #fff;
  font-family: "Miriam Libre", sans-serif;
  font-size: 20px;
  padding: 20px;
}

.partner-profile {
  padding-bottom: 50px;
}

.partner-profile-header {
  border-bottom: 1px solid #D9DBDA;
  padding: 10px 0px 20px 0px;
  margin-bottom: 40px;
}

.partner-profile-bio p {
  line-height: 28px;
}

.partner-profile-img img {
  max-width: 100%;
  height: auto;
}

.partner-logo {
  float: left;
}

.partners .flag {
  font-size: 30px;
  background: rgba(255, 255, 255, 0.6);
  padding: 16px;
  border-radius: 50%;
  line-height: 30px;
  bottom: 20px;
  right: 20px;
  position: absolute;
}

.partner-ctas {
  float: right;
}

.partner-ctas .btn {
  margin-left: 10px;
}

.partner-ctas .btn-primary {
  border: 2px solid #159f23;
}

.partner-ctas .btn-default {
  background-color: white;
  border: 2px solid #159f23;
  color: #159f23;
}

.partner-ctas .partner-ctas-visible-mobile {
  display: none;
}

.partner-social ul {
  -webkit-margin-before: 1em;
  -webkit-margin-after: 0em;
  -webkit-padding-start: 0px;
}

.partner-proficiencies-list {
  list-style-type: none;
  padding-left: 2.5em;
  text-align: left;
}

.partner-proficiencies-list > li {
  position: relative;
  margin-bottom: 1em;
}

.partner-proficiencies-list > li:before {
  position: absolute;
  left: -2.5em;
  top: 0;
  content: url(/assets/svg/icon-check.svg);
  width: 1.5em;
  height: 1.5em;
}

.flex {
  display: flex;
}

.flex-fill {
  flex: 1;
}

.partner-social-list {
  margin-bottom: 2rem;
}

.partner-social-list ul {
  padding-left: 0;
}

.partner-social-list li {
  list-style: none;
  display: inline-block;
  margin-right: 1.5em;
}

@media (max-width: 1024px) {
  .flex-row {
    display: flex;
    flex-direction: column;
  }
  .mr-2 {
    margin-right: 0px;
  }
  .partner-card-lg {
    width: 100%;
    height: 200px;
  }
  .partner-card-md {
    width: 100%;
    height: 200px;
  }
  .partner-card-sm {
    width: 100%;
    height: auto;
  }
  .partner-card {
    display: flex;
    margin-bottom: 20px;
    flex-direction: column;
  }
  .partner-card-banner {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .partner-logo, .partner-ctas {
    float: inherit;
  }
  .partner-logo {
    margin-bottom: 20px;
  }
  .partner-ctas .btn {
    margin-right: 10px;
    margin-left: 0px;
  }
  .partner-proficiencies .flex {
    display: inherit;
  }
  .partner-social {
    margin-bottom: 2.5em;
  }
}

@media (max-width: 595px) {
  .partner-card-banner img {
    max-width: 90%;
  }
  .partner-card-call {
    height: auto;
  }
  .partner-ctas .partner-ctas-visible-desktop {
    display: none;
  }
  .partner-ctas .partner-ctas-visible-mobile {
    display: inline;
  }
}

.docs article {
  padding: 25px 125px 50px 50px;
  margin-left: 300px;
}

.docs article ul {
  font-size: 13px;
}

.docs article .content-list ul li {
  margin: 8px;
  line-height: 1.65;
}

.docs article p {
  font-size: 18px;
  line-height: 1.9;
}

.docs article h1 + ul {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 14px;
  font-weight: bold;
  line-height: 1.8;
  padding-bottom: 50px;
  border-bottom: 1px solid #f0f2f1;
  -webkit-font-smoothing: antialiased;
}

.docs article h1 + ul li:before {
  content: "# ";
  margin-right: .25em;
  color: #159f23;
  opacity: .3;
}

.docs article h1 + ul li a {
  text-decoration: none;
}

.docs article li > ul {
  font-size: 13px;
  font-weight: 400;
  list-style: none;
  line-height: 1.7;
}

.docs article h2:first-of-type {
  margin-top: 15px;
}

.docs article h2 {
  font-size: 30px;
  font-weight: 400;
  margin-top: 55px;
  margin-bottom: 15px;
  position: relative;
}

.docs article h2 a,
.docs article h2 a:hover {
  color: #525252;
  text-decoration: none;
}

.docs article h2 a:before {
  content: "#";
  margin-left: -25px;
  position: absolute;
  font-size: 28px;
  color: #159f23;
  opacity: .6;
}

.docs article h3 {
  font-size: 24px;
  font-weight: 400;
  margin-top: 45px;
  margin-bottom: 10px;
}

.docs article h4 {
  font-size: 17px;
  font-weight: 700;
  margin-top: 35px;
  margin-bottom: 10px;
}

.docs article a {
  text-decoration: underline;
}

.docs article a:hover {
  color: #0f7219;
}

.docs article blockquote a:hover {
  color: #4ce75c;
}

.docs article table {
  border-collapse: collapse;
  width: 100%;
  font-size: 13px;
}

.docs article table th,
.docs article table td {
  border: 1px solid #dee0df;
  padding: 10px;
  text-align: left;
}

.docs article table th {
  font-size: 16px;
}

.docs blockquote.has-icon {
  position: relative;
}

.docs blockquote.has-icon.video, .docs blockquote.has-icon.laracast {
  background: rgba(103, 58, 183, 0.62);
}

.docs blockquote.has-icon.tip {
  background: #64B5F6;
}

.docs blockquote.has-icon p {
  padding-left: 40px;
}

.docs blockquote.has-icon .flag {
  position: absolute;
  width: 30px;
  left: 15px;
  top: 10px;
}

.docs blockquote.has-icon .flag svg {
  width: 24px;
  height: 24px;
}

.docs .svg svg {
  width: 24px;
}

.autocomplete-wrapper .h1 {
  font-size: 16px;
  font-weight: bold;
}

.autocomplete-wrapper .h2 {
  font-size: 16px;
  display: inline-block;
}

.autocomplete-wrapper .h2 .hash {
  color: #F8A2A9;
}

.autocomplete-wrapper .h3 {
  font-size: 16px;
  display: inline-block;
}

.autocomplete-wrapper .h4 {
  font-size: 16px;
  display: inline-block;
}

.autocomplete-wrapper .content {
  font-size: 13px;
  background-color: rgba(238, 238, 238, 0.35);
  padding: 10px;
  border-radius: 3px;
  margin-left: 3px;
  margin-top: 14px;
}

.twitter-typeahead {
  width: 100%;
  position: relative;
}

.twitter-typeahead .tt-input, .twitter-typeahead .tt-hint {
  width: 100%;
  margin: 0;
  padding: 8px 12px;
  border: 2px solid #CCC;
  outline: none;
